import React from 'react';
import '../css/section.css';
import Carousel from 'react-bootstrap/Carousel';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChevronLeft, faChevronRight } from '@fortawesome/free-solid-svg-icons';
import { HiDownload } from "react-icons/hi"; 
import { SiAndroidstudio } from "react-icons/si";


function AppSection(props) {

  let appTitle = props.app.appTitle;
  let appDescription = props.app.appDescription;
  let sectionId = props.app.sectionId;
  let features = props.app.features;
  let techPoints = props.app.techPoints;
  let madeWith = props.app.madeWith;

  //let appLink = require('../apks/' + props.app.fileName);
  var images = [];
  if(props.app.images !== undefined){
    props.app.images.map(image =>{
      images.push(require('../images/' + image));
    })
  }

  let videoPath = require('../videos/' + props.app.videoPath);  

  var classes = sectionId + " vh-min-100 d-flex justify-content-around section-container";

  var previousIcon = <span className="btn btn-dark button-left">
    <FontAwesomeIcon icon={faChevronLeft}/>
  </span>

  var NextIcon = <span className="btn btn-dark button-right">
      <FontAwesomeIcon icon={faChevronRight} />
    </span>

  return (
    <div className={classes} id={sectionId}>

      <div className="description col-12 col-lg-4">
        <div className="card">
          <div className="card-header section1 d-flex justify-content-between">
              {appTitle} 
              <div className="appLink">
                <a href="#" download><HiDownload className="mr-2"/>Android App</a>
              </div>
          </div>
          {/* App Description Part */}
          <div className="card-body section1">
              <p>{appDescription}</p>
              {/* App Features part */}
              <div>
                <p className="app-heading">Features</p>
                <ul>
                  {
                    features.map(feature => {
                      return (<li>{feature}</li>)
                    })
                  }
                </ul>
              </div>
              {/* App Technology */}
              <div>
                <p className="app-heading">Technology</p>
                <ul>
                  {
                    techPoints.map(point => {
                      return (<li>{point}</li>)
                    })
                  }
                </ul>
              </div>
            </div>     
            <div className="card-footer text-center">
              Developed With <span className="app-heading">{madeWith}</span>
               in <span className="app-heading">Android Studio</span> <SiAndroidstudio/>
            </div>  
        </div>
      </div>

        <div className="video">
          <div className="left-btns">
              <div className="left-btn1"></div>
              <div className="left-btn2"></div>
              <div className="left-btn3"></div>
          </div>
          <div className="bg-1">
              <div className="notch">
                  <div className="speaker"></div>
                  <div className="sensor"></div>
              </div>
              <video controls preload="metadata">
                  <source src={videoPath} type="video/mp4"/>
              </video>
          </div>
          <div className="right-btns">
              <div className="right-btn1"></div>
          </div>
        </div>


        <div id="images-main" className="images-main carousel slide" data-ride="carousel">
            <div className="left-btns">
                <div className="left-btn1"></div>
                <div className="left-btn2"></div>
                <div className="left-btn3"></div>
            </div>
            <div className="bg-1">
                <div className="notch">
                    <div className="speaker"></div>
                    <div className="sensor"></div>
                </div>

                <div className="images carousel-inner">
                  <Carousel 
                    prevIcon={previousIcon}
                    nextIcon={NextIcon}>
                      {
                        images.map(image =>{
                          return (
                            <Carousel.Item >
                              <img
                                className="d-block w-100"
                                src={image}
                                alt="First slide"
                              />
                            </Carousel.Item>
                          )
                        })
                      }                                          
                  </Carousel>
                </div>
            </div>
            <div className="right-btns">
                <div className="right-btn1"></div>
            </div>
        </div>
      </div>
  );
}

export default AppSection;
