import React, { Component } from 'react';
import AppSection from './components/AppSection';
import ProfileSection from './components/ProfileSection';
import Navigation from './components/Navigation';



class App extends Component {

  constructor() {
    super();
    this.state = {
      appsList: [],
    };
  }

  componentDidMount() {
    this.setState({ appsList: require('./data/data.json') });
  }

  render() {

    return (
      <div className="h-screen">
        <Navigation appList={this.state.appsList}/>    
        {          
          this.state.appsList.map(app => {
            return (
              <AppSection 
                app={app}
                key={app.sectionId}
              />
            )
          })
        }   
        <ProfileSection/>
        <div className="card-footer main text-center">
            Developed With <span className="app-heading">React js </span>
            in <span className="app-heading">Visual Studio</span>
        </div> 

      </div>
    );
  }
  
}

export default App;
